<template>
  <v-container class = "chat-panel">
    <v-container class="output-container">
      <v-card>
        <v-card-text class="output">
          {{ outputText }}
        </v-card-text>
      </v-card>
    </v-container>
  <v-container fluid class="floating-textarea">
      <v-textarea
        counter
        clearable
        elevation-6
        clear-icon="mdi-close-circle"
        :append-inner-icon="inputValue ? 'mdi-send' : 'mdi-send-lock'"
        rows="4"
        row-height="5"
        label="Input your text."
        :rules="rules"
        :model-value="inputValue"
        v-char-counter
        @keydown.enter="submitData"
        @click:append-inner="submitData"
        @input="updateInputValue"
        theme="dark"
      >
      </v-textarea>
</v-container>
  </v-container>
  
</template>

<script>
import { defineComponent, ref } from "vue";
import axios from "axios";

export default defineComponent({
  directives: {
    charCounter: {
      mounted(el) {
        const textarea = el.querySelector('textarea');
        const counter = el.querySelector('.v-counter');

        textarea.addEventListener('input', () => {
          const charCount = textarea.value.length;
          counter.textContent = charCount;
        });
      },
    },
  },
  emits: ['submit'],
  setup() {
    const outputText = ref("");
    const additionalData = ref("");
    const rules = ref([(v) => v.length <= 5000 || '最多输入5000个字符']);
    const inputValue = ref("");

    const updateInputValue = (event) => {
      inputValue.value = event.target.value;
    };

    const submitData = async () => {
      if (inputValue.value) {
        const requestData = {
          url: "api/data", // Replace with your API endpoint
          text: inputValue.value,
        };

        try {
          const response = await axios.post(requestData.url, requestData);
          outputText.value = response.data.content;
          additionalData.value = response.data.additionalData;
        } catch (error) {
          console.error(error);
        }
        inputValue.value = "";
      }
    };

    return {
      outputText,
      submitData,
      rules,
      inputValue,
      updateInputValue,
    };
  },
});
</script>

<style>

.chat-panel {
  position: flex;
  min-height: 100vh;
}

.output-container{
  width: calc(90% - 10%); /* 设置宽度为页面宽度的 90%，并减去左右两侧的空白区域 */
  max-width: 400px; /* 设置最大宽度为 400px */
  height: 70vh;
}

.output{
  width: calc(90% - 10%); /* 设置宽度为页面宽度的 90%，并减去左右两侧的空白区域 */
  max-width: 400px; /* 设置最大宽度为 400px */
  height: 65vh;
}

.floating-textarea {
  height:20vh;
  width: calc(90% - 10%); /* 设置宽度为页面宽度的 90%，并减去左右两侧的空白区域 */
  max-width: 400px; /* 设置最大宽度为 400px */
  z-index: 999; /* 设置悬浮文本区域的层级 */
}


@media (max-width: 600px) {
  .floating-textarea {
    right: 10px; /* 在小屏幕设备上调整右外边距 */
    width: calc(100% - 20px); /* 在小屏幕设备上调整宽度 */
  }
}
</style>
